* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background: #b0b9a0; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #d4d3dd, #efefbb); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #d4d3dd,
    #efefbb
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding-inline-start: 10px;
}

.country__item {
  display: flex;
  align-items: center;
  font-weight: 500;
  margin-bottom: 10px;
}

.country__name {
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 0;
}

.country__item img {
  display: block;
  width: 40px;
  height: 25px;
  margin-right: 15px;
}

.info-list {
  list-style: none;
}

.country {
  display: flex;
  align-items: center;
  padding-left: 15px;
  margin-bottom: 20px;
}

.country img {
  display: block;
  width: 40px;
  height: 25px;
  margin-right: 15px;
}

.country__id {
  font-size: 40px;
  font-weight: 500;
  margin: 0;
}

.info-list {
  margin-block-start: 0;
  margin-bottom: 0;
  padding-left: 15px;
}

.info-list__item {
  margin-bottom: 15px;
}

.info-list__span {
  font-weight: 700;
  font-size: 16px;
}
